import template from './template';
// import { alertEmphasis } from '../../util';

export default class FiberCable extends HTMLElement {
  static #LOCAL_STORAGE_ITEM = 'com.codedbykevin.telcotools.fiber-cable';

  static #magic = { binderVal: 12, strandRange: 431 };

  #dom = null;

  #initialized = false;

  constructor() {
    super();
  }

  connectedCallback() {
    if (this.#initialized !== true) {
      const state = FiberCable.#getState();

      this.innerHTML = template.html(state);

      this.#dom = template.mapDOM(this);

      this.#initialized = true;
    }

    this.#addHandlers();
  }

  disconnectedCallback() {
    this.#removeHandlers();
  }

  #addHandlers() {
    this.#dom.fiberCableColor.onchange = (e) => this.#onchange(e);

    this.#dom.fiberCableAssignment.oninput = (e) => this.#oninput(e);
  }

  #removeHandlers() {
    this.#dom.fiberCableColor.onchange = null;

    this.#dom.fiberCableAssignment.oninput = null;
  }

  #onchange(e) {
    this.#clearErrors();

    this.#calculateFromColors();
  }

  #oninput(e) {
    this.#clearErrors();

    this.#calculateFromAssignment();
  }

  #clearErrors() {
    this.#dom.fiberCableStartError.innerText = '';

    this.#dom.fiberCableStartError.classList.remove('is-visible');

    this.#dom.fiberCableStrandError.innerText = '';

    this.#dom.fiberCableStrandError.classList.remove('is-visible');
  }

  #validateStart() {
    const min = parseInt(this.#dom.fiberCableStart.min);

    const max = parseInt(this.#dom.fiberCableStart.max);

    const value = parseInt(this.#dom.fiberCableStart.value);

    if (isNaN(value) || value < min || value > max) {
      return [new Error(`"Start" must range from ${min} to ${max}`), null];
    }

    return [null, { min, max, value }];
  }

  #validateStrand() {
    const min = parseInt(this.#dom.fiberCableStrand.min);

    const max = parseInt(this.#dom.fiberCableStrand.max);

    const value = parseInt(this.#dom.fiberCableStrand.value);

    if (isNaN(value) || value < min || value > max) {
      return [new Error(`"Strand" must range from ${min} to ${max}`), null];
    }

    return [null, { min, max, value }];
  }

  #calculateFromColors() {
    const [err, result] = this.#validateStart();

    if (err) {
      this.#dom.fiberCableStartError.innerText = err.message || err.toString();

      this.#dom.fiberCableStartError.classList.add('is-visible');

      return;
    }

    const { value: startNum } = result;

    const binderNum = parseInt(this.#dom.fiberCableBinder.value);

    const positionNum = parseInt(this.#dom.fiberCablePosition.value);

    let strandNum = startNum;

    strandNum += binderNum;

    strandNum += positionNum;

    this.#dom.fiberCableStrand.value = strandNum;

    FiberCable.#setState({
      startNum,
      strandNum,
      binderNum,
      positionNum,
    });
  }

  #calculateFromAssignment() {
    let [err, result] = this.#validateStart();

    if (err) {
      this.#dom.fiberCableStartError.innerText = err.message || err.toString();

      this.#dom.fiberCableStartError.classList.add('is-visible');

      return;
    }

    const { value: startNum } = result;

    this.#dom.fiberCableStrand.min = startNum;

    this.#dom.fiberCableStrand.max = startNum + FiberCable.#magic.strandRange;

    [err, result] = this.#validateStrand();

    if (err) {
      this.#dom.fiberCableStrandError.innerText = err.message || err.toString();

      this.#dom.fiberCableStrandError.classList.add('is-visible');

      return;
    }

    const { value: strandNum } = result;

    const index = strandNum - startNum;

    const binderNum = ~~(index / FiberCable.#magic.binderVal) * FiberCable.#magic.binderVal;

    const positionNum = index - binderNum;

    this.#dom.fiberCableBinder.value = binderNum;

    this.#dom.fiberCablePosition.value = positionNum;

    FiberCable.#setState({
      startNum,
      strandNum,
      binderNum,
      positionNum,
    });
  }

  static #setState(state) {
    localStorage.setItem(FiberCable.#LOCAL_STORAGE_ITEM, JSON.stringify(state));
  }

  static #getState() {
    const item = localStorage.getItem(FiberCable.#LOCAL_STORAGE_ITEM);

    if (!item) {
      return {
        startNum: 1,
        strandNum: 1,
        binderNum: 0,
        positionNum: 0,
      };
    }

    return JSON.parse(item);
  }
}

if (!customElements.get('fiber-cable')) {
  customElements.define('fiber-cable', FiberCable);
}
