const positionOptions = [
  { text: '1', value: 0 },
  { text: '2', value: 1 },
  { text: '3', value: 2 },
  { text: '4', value: 3 },
];

const pairRange = 2023; // max value can be 2024

const optionsFromArray = (arr, val) => {
  let options = '';
  arr.forEach((item) => {
    options += `<option value="${item.value}" ${item.value === val ? 'selected="selected"' : ''}>${item.text}</option>`;
  });
  return options;
};

export default {
  mapDOM(scope) {
    return {
      lightspanPortEquipment: scope.querySelector('#lightspanPortEquipment'),
      lightspanPortBank: scope.querySelector('#lightspanPortBank'),
      lightspanPortSlot: scope.querySelector('#lightspanPortSlot'),
      lightspanPortPosition: scope.querySelector('#lightspanPortPosition'),
      lightspanPortAssignment: scope.querySelector('#lightspanPortAssignment'),
      lightspanPortStart: scope.querySelector('#lightspanPortStart'),
      lightspanPortPair: scope.querySelector('#lightspanPortPair'),
      lightspanPortStartError: scope.querySelector('#lightspanPortStartError'),
      lightspanPortPairError: scope.querySelector('#lightspanPortPairError'),
    };
  },

  html(state) {
    return `<div id="lightspanPort" class="app flex-container align-center-middle">
      <div id="lightspanPortEquipment" class="item bg-light-blue radius bordered shadow landscape-equal">
        <div class="input-group">
          <label class="input-group-label" for="lightspanPortBank">Bank:</label>
          <select class="input-group-field" id="lightspanPortBank">
            ${optionsFromArray(bankOptions, state.bankNum)}
          </select>
        </div>
        <div class="input-group">
          <label class="input-group-label" for="lightspanPortSlot">Slot:</label>
          <select class="input-group-field" id="lightspanPortSlot">
            ${optionsFromArray(slotOptions, state.slotNum)}
          </select>
        </div>
        <div class="input-group">
          <label class="input-group-label" for="lightspanPortPosition">Position:</label>
          <select class="input-group-field" id="lightspanPortPosition">
            ${optionsFromArray(positionOptions, state.positionNum)}
          </select>
        </div>
      </div>
      <span class="equals"></span>
      <div id="lightspanPortAssignment" class="item bg-light-blue radius bordered shadow landscape-equal">
        <div class="input-group">
          <label class="input-group-label" for="lightspanPortStart">Start:</label>
          <input class="input-group-field" id="lightspanPortStart" type="number" inputmode="numeric" placeholder="1234" min="1" max="9999" maxlength="4" pattern="number" value="${
            state.startNum
          }"/>
        </div>
        <span id="lightspanPortStartError" class="form-error"></span>
        <div class="input-group">
          <label class="input-group-label" for="lightspanPortPair">Pair:</label>
          <input class="input-group-field" id="lightspanPortPair" type="number" inputmode="numeric" placeholder="1234" min="${
            state.startNum
          }" max="${state.startNum + pairRange}" maxlength="5" pattern="number" value="${state.pairNum}"/>
        </div>
        <span id="lightspanPortPairError" class="form-error"></span>
      </div>
    </div>`;
  },
};
