'use strict';

import { installed, iosCanInstall } from './pwaInfo';

import IosPwaInstall from '../components/ios-pwa-install/ios-pwa-install';
import PwaInstall from '../components/pwa-install/pwa-install';

if (installed === false) {
  // console.log('not installed');

  if (iosCanInstall === true) {
    document.body.appendChild(new IosPwaInstall());
  } else {
    window.addEventListener(
      'beforeinstallprompt',
      (e) => {
        e.preventDefault();
        document.body.appendChild(new PwaInstall(e));
      },
      { capture: false, once: true, passive: true }
    );
  }
} else {
  // console.log('already installed');
}
