import template from './template';
import { CenteredReveal } from '../../lib/CenteredReveal';
import { $ } from '../../vendor/jquery';
import { globalEventMap } from '../../lib/globalEventMap';

export default class PwaInstall extends HTMLElement {
  static #LOCAL_STORAGE_ITEM = 'com.codedbykevin.telcotools.pwa-install';

  static #MAGIC = {
    minTimeout: 60 * 1000, // = 1 minute
  };

  #dom = null;

  #initialized = false;

  #deferred = null;

  #modal = null;

  #resizeSymbol = null;

  #setTimeout = null;

  #timeout = null;

  constructor(deferred) {
    super();

    this.#deferred = deferred;
  }

  connectedCallback() {
    if (this.#initialized !== true) {
      this.#timeout = PwaInstall.#MAGIC.minTimeout;

      this.innerHTML = template.html();

      this.#dom = template.mapDOM(this);

      this.#modal = new CenteredReveal($(this.#dom.pwaInstallModal), {
        appendTo: this,
        closeOnClick: false,
        additionalOverlayClasses: '',
      });

      this.#resizeSymbol = Symbol('resize');

      this.#initialized = true;
    }

    this.#addHandlers();
    this.#startTimer();
  }

  disconnectedCallback() {
    this.#removeHandlers();
    this.#stopTimer();
  }

  #startTimer() {
    this.#setTimeout = setTimeout(() => {
      this.#modal.open();
      this.#setTimeout = null;
    }, this.#timeout);
  }

  #stopTimer() {
    clearTimeout(this.#setTimeout);
    this.#setTimeout = null;
  }

  #addHandlers() {
    const onResizeBound = () => this.#onResize();
    globalEventMap.set(this.#resizeSymbol, onResizeBound);
    window.addEventListener('resize', onResizeBound);

    $(this).on('closed.zf.reveal', this.#onClosed);
    this.#dom.pwaInstallButton.onclick = (e) => this.#onClick(e);
  }

  #removeHandlers() {
    window.removeEventListener('resize', globalEventMap.get(this.#resizeSymbol));
    globalEventMap.delete(this.#resizeSymbol);

    $(this).off('closed.zf.reveal', this.#onClosed);
    this.#dom.pwaInstallButton.onclick = null;
  }

  async #onClick() {
    this.#dom.pwaInstallButton.disabled = true;

    try {
      const { outcome, platform } = await this.#deferred.prompt();
    } catch (err) {
      console.error(err);
      alert(err);
    } finally {
      this.#modal.close();
    }
  }

  #onResize() {
    this.#modal.updatePosition();
  }

  #onClosed() {
    this.remove();
  }

  static #setState(state) {
    localStorage.setItem(PwaInstall.#LOCAL_STORAGE_ITEM, JSON.stringify(state));
  }

  static #getState() {
    const item = localStorage.getItem(PwaInstall.#LOCAL_STORAGE_ITEM);

    if (!item) {
      return {};
    }
  }
}

if (!customElements.get('pwa-install')) {
  customElements.define('pwa-install', PwaInstall);
}
