'use strict';

import { Reveal } from '../vendor/foundation';

import { $ } from '../vendor/jquery';

class CenteredReveal extends Reveal {
  _updatePosition() {
    const height = this.$element.outerHeight();

    const outerHeight = $(window).height();

    const top = height < outerHeight ? ~~((outerHeight - height) / 2) : 0;

    this.$element.css({ top: top + 'px' });
  }

  updatePosition() {
    this._updatePosition();
  }
}

export { CenteredReveal };
