import template from './template';

export default class NavMenu extends HTMLElement {
  static #LOCAL_STORAGE_ITEM = 'com.codedbykevin.telcotools.nav-menu';

  static #DEFAULT = {
    element: '<even-pic></even-pic>',
    title: 'Even PIC Color to Pair',
  };

  #dom = null;

  #initialized = false;

  #containerElem = null;

  #titleElem = null;

  #element = null;

  #title = null;

  constructor() {
    super();
  }

  connectedCallback() {
    if (this.#initialized !== true) {
      const { element, title } = NavMenu.#getState();

      this.#element = element;

      this.#title = title;

      const containerSelector = this.getAttribute('container-selector');

      const titleSelector = this.getAttribute('title-selector');

      this.#containerElem = document.querySelector(containerSelector) || document.querySelector('div#main');

      this.#titleElem = document.querySelector(titleSelector) || document.querySelector('div#title');

      this.innerHTML = template.html();

      this.#dom = template.mapDOM(this);

      this.#initialized = true;

      this.#renderHtml();
    }

    this.#addHandlers();
  }

  disconnectedCallback() {
    this.#removeHandlers();
  }

  /*  attributeChangedCallback(name, oldValue, newValue) {
    console.log({ name, oldValue, newValue });
  }

  static get observedAttributes() {
    return ['container-selector', 'title-selector'];
  }*/

  #addHandlers() {
    for (const button of this.#dom.buttons) {
      button.onclick = (e) => this.#onclick(e);
    }
  }

  #removeHandlers() {
    for (const button of this.#dom.buttons) {
      button.onclick = null;
    }
  }

  #onclick(e) {
    const {
      currentTarget: {
        innerText: title,
        dataset: { element },
      },
    } = e;

    let dirty = false;

    if (this.#element !== element) {
      this.#element = element;
      dirty = true;
    }

    if (this.#title !== title) {
      this.#title = title;
      dirty = true;
    }

    if (dirty === false) {
      return;
    }

    this.#renderHtml();

    NavMenu.#setState({
      element: this.#element,
      title: this.#title,
    });
  }

  #renderHtml() {
    if (!this.#containerElem || !this.#titleElem) {
      return (document.body.innerHTML =
        '<h1>nav-menu error:</h1><p>nav-menu requires a valid container-selector and title-selector</p>');
    }

    this.#containerElem.innerHTML = this.#element;

    this.#titleElem.textContent = this.#title;
  }

  static #setState(state) {
    localStorage.setItem(NavMenu.#LOCAL_STORAGE_ITEM, JSON.stringify(state));
  }

  static #getState() {
    const item = localStorage.getItem(NavMenu.#LOCAL_STORAGE_ITEM);

    if (item === null) {
      return NavMenu.#DEFAULT;
    }

    const { element = NavMenu.#DEFAULT.element, title = NavMenu.#DEFAULT.title } = JSON.parse(item);

    return {
      element,
      title,
    };
  }
}

if (customElements.get('nav-menu') === undefined) {
  customElements.define('nav-menu', NavMenu);
}
