const pairRange = 795; // max value can be 796

const optionsFromArray = (arr, val) => {
  let options = '';
  arr.forEach((item) => {
    options += `<option value="${item.value}" ${item.value === val ? 'selected="selected"' : ''}>${item.text}</option>`;
  });
  return options;
};

export default {
  mapDOM(scope) {
    return {
      vradPortEquipment: scope.querySelector('#vradPortEquipment'),
      vradPortBank: scope.querySelector('#vradPortBank'),
      vradPortSlot: scope.querySelector('#vradPortSlot'),
      vradPortPosition: scope.querySelector('#vradPortPosition'),
      vradPortAssignment: scope.querySelector('#vradPortAssignment'),
      vradPortStart: scope.querySelector('#vradPortStart'),
      vradPortPair: scope.querySelector('#vradPortPair'),
      vradPortStartError: scope.querySelector('#vradPortStartError'),
      vradPortPairError: scope.querySelector('#vradPortPairError'),
    };
  },

  html(state) {
    return `<div id="vradPort" class="app flex-container align-center-middle">
      <div id="vradPortEquipment" class="item bg-light-blue radius bordered shadow landscape-equal">
        <div class="input-group">
          <label class="input-group-label" for="vradPortBank">Bank:</label>
          <select class="input-group-field" id="vradPortBank">
            ${optionsFromArray(bankOptions, state.bankNum)}
          </select>
        </div>
        <div class="input-group">
          <label class="input-group-label" for="vradPortSlot">Slot:</label>
          <select class="input-group-field" id="vradPortSlot">
            ${optionsFromArray(slotOptions, state.slotNum)}
          </select>
        </div>
        <div class="input-group">
          <label class="input-group-label" for="vradPortPosition">Position:</label>
          <select class="input-group-field" id="vradPortPosition">
            ${optionsFromArray(positionOptions, state.positionNum)}
          </select>
        </div>
      </div>
      <span class="equals"></span>
      <div id="vradPortAssignment" class="item bg-light-blue radius bordered shadow landscape-equal">
        <div class="input-group">
          <label class="input-group-label" for="vradPortStart">Start:</label>
          <input class="input-group-field" id="vradPortStart" type="number" inputmode="numeric" placeholder="1234" min="1" max="9999" maxlength="4" pattern="number" value="${
            state.startNum
          }"/>
        </div>
        <span id="vradPortStartError" class="form-error"></span>
        <div class="input-group">
          <label class="input-group-label" for="vradPortPair">Pair:</label>
          <input class="input-group-field" id="vradPortPair" type="number" inputmode="numeric" placeholder="1234" min="${
            state.startNum
          }" max="${state.startNum + pairRange}" maxlength="5" pattern="number" value="${state.pairNum}"/>
        </div>
        <span id="vradPortPairError" class="form-error"></span>
      </div>
    </div>`;
  },
};
