import template from './template';

import { CenteredReveal } from '../../lib/CenteredReveal';
import { globalEventMap } from '../../lib/globalEventMap';
import { $ } from '../../vendor/jquery';

/*

position --> location --> savedLocations

position is raw details from geolocation {accuracy,latitude,longitude,speed,timestamp,}

location is created from position {accuracy,latitude,longitude,distance,}

savedLocations is array of location(s) [location,...]

based on accuracy, speed, and distance, `add` button will be enabled/disabled

based on savedLocations.length, `delete` and `reset` buttons will be enabled/disabled

todo: link to show popup list of saved locations

todo: links to maps with gps coords

 */

export default class GpsMeasure extends HTMLElement {
  // static private vars

  static #LOCAL_STORAGE_ITEM = 'com.codedbykevin.telcotools.gps-measure';

  static #WATCH_POSITION_OPTIONS = {
    enableHighAccuracy: true,
    timeout: 10000, // 5000
    maximumAge: 0,
  };

  static #MAGIC = {
    mpsToKph: 3.6, // meters per second to kilometers per hour
    mpsToMph: 2.23694, // meters per second to miles per hour
    mToFt: 3.28084, // meters to feet
    speedMax: 0.44704, // = 1 mph
    accuracyMax: 15.24, // = 50 ft
    distanceMin: 4.572, // = 15 ft
  };

  // private vars

  #dom = null;

  #initialized = false;

  #watchID = null;

  #position = null;

  #location = null;

  #savedLocations = [];

  #removeModal = null;

  #resetModal = null;

  #resumeModal = null;

  #resizeSymbol = null;

  #visibilityChangeSymbol = null;

  #shouldResume = false;

  // public methods

  constructor() {
    super();
  }

  connectedCallback() {
    if (this.#initialized !== true) {
      this.#savedLocations = GpsMeasure.#getState();

      this.innerHTML = template.html();

      this.#dom = template.mapDOM(this);

      this.#removeModal = new CenteredReveal($(this.#dom.removeModal), {
        appendTo: '#gpsMeasure',
        closeOnClick: false,
        additionalOverlayClasses: '', // gets added to reveal's parent div (reveal-overlay)
      });

      this.#resetModal = new CenteredReveal($(this.#dom.resetModal), {
        appendTo: '#gpsMeasure',
        closeOnClick: false,
        additionalOverlayClasses: '',
      });

      this.#resumeModal = new CenteredReveal($(this.#dom.resumeModal), {
        appendTo: '#gpsMeasure',
        closeOnClick: false,
        additionalOverlayClasses: '',
      });

      this.#resizeSymbol = Symbol('resize');

      this.#visibilityChangeSymbol = Symbol('visibilityChange');

      this.#renderHtml('Ready');

      this.#initialized = true;
    }

    this.#addHandlers();
  }

  disconnectedCallback() {
    this.#removeHandlers();

    this.#stopGeolocation('Disconnected');
  }

  // static private methods

  static #setState(state) {
    localStorage.setItem(GpsMeasure.#LOCAL_STORAGE_ITEM, JSON.stringify(state));
  }

  static #getState() {
    const item = localStorage.getItem(GpsMeasure.#LOCAL_STORAGE_ITEM);

    if (!item) {
      return [];
    }

    return JSON.parse(item);
  }

  static #distanceBetweenCoords(lat1, lon1, lat2, lon2) {
    const RADIUS_OF_EARTH_IN_METERS = 6371000;
    const TO_RADIANS = 0.017453292519943295; // Math.PI / 180
    const diffLatRadHalf = ((lat2 - lat1) * TO_RADIANS) / 2;
    const diffLonRadHalf = ((lon2 - lon1) * TO_RADIANS) / 2;
    const lat1Rad = lat1 * TO_RADIANS;
    const lat2Rad = lat2 * TO_RADIANS;
    const a =
      Math.sin(diffLatRadHalf) * Math.sin(diffLatRadHalf) +
      Math.sin(diffLonRadHalf) * Math.sin(diffLonRadHalf) * Math.cos(lat1Rad) * Math.cos(lat2Rad);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
    return RADIUS_OF_EARTH_IN_METERS * c;
  }

  // private methods

  #addHandlers() {
    const onResizeBound = () => this.#onResize();

    globalEventMap.set(this.#resizeSymbol, onResizeBound);

    window.addEventListener('resize', onResizeBound);

    const onVisibilityChangeBound = () => this.#onVisibilityChange();

    globalEventMap.set(this.#visibilityChangeSymbol, onVisibilityChangeBound);

    document.addEventListener('visibilitychange', onVisibilityChangeBound);

    this.#dom.start.onclick = () => this.#startGeolocation();

    this.#dom.stop.onclick = () => this.#stopGeolocation();

    this.#dom.add.onclick = () => this.#addGeolocation();

    this.#dom.remove.onclick = () => this.#removeGeolocation();

    this.#dom.reset.onclick = () => this.#resetGeolocation();

    this.#dom.removeConfirm.onclick = () => this.#removeConfirmGeolocation();

    this.#dom.resetConfirm.onclick = () => this.#resetConfirmGeolocation();

    this.#dom.resumeConfirm.onclick = () => this.#startGeolocation('Resuming');
  }

  #removeHandlers() {
    window.removeEventListener('resize', globalEventMap.get(this.#resizeSymbol));

    globalEventMap.delete(this.#resizeSymbol);

    document.removeEventListener('visibilitychange', globalEventMap.get(this.#visibilityChangeSymbol));

    globalEventMap.delete(this.#visibilityChangeSymbol);

    this.#dom.start.onclick = null;

    this.#dom.stop.onclick = null;

    this.#dom.add.onclick = null;

    this.#dom.remove.onclick = null;

    this.#dom.reset.onclick = null;

    this.#dom.removeConfirm.onclick = null;

    this.#dom.resetConfirm.onclick = null;
  }

  #onVisibilityChange() {
    switch (document.visibilityState) {
      case 'hidden':
        if (this.#watchID !== null) {
          this.#shouldResume = true;
        }

        this.#stopGeolocation('Stopped', true);

        this.#removeModal.close();

        this.#resetModal.close();
        break;

      case 'visible':
        if (this.#shouldResume === true) {
          this.#shouldResume = false;

          this.#resumeModal.open();
        }

        break;
    }
  }

  #onResize() {
    this.#removeModal.updatePosition();

    this.#resetModal.updatePosition();

    this.#resumeModal.updatePosition();
  }

  #startGeolocation(status = 'Running') {
    if (this.#watchID === null) {
      this.#watchID = navigator.geolocation.watchPosition(
        (position) => this.#watchPositionSuccess(position),
        (error) => this.#watchPositionError(error),
        GpsMeasure.#WATCH_POSITION_OPTIONS
      );

      this.#dom.start.classList.add('is-hidden');

      this.#dom.stop.classList.remove('is-hidden');

      this.#renderHtml(status);
    }
  }

  #stopGeolocation(status = 'Stopped') {
    if (this.#watchID !== null) {
      navigator.geolocation.clearWatch(this.#watchID);

      this.#watchID = null;

      this.#dom.stop.classList.add('is-hidden');

      this.#dom.start.classList.remove('is-hidden');

      this.#position = null;

      this.#location = null;

      this.#renderHtml(status);
    }
  }

  #addGeolocation() {
    if (this.#location !== null) {
      const { accuracy, latitude, longitude, distance } = this.#location;
      this.#location = null;
      this.#savedLocations.push({ accuracy, latitude, longitude, distance });
      this.#renderHtml('Pending Update');
      GpsMeasure.#setState(this.#savedLocations);
    }
  }

  #removeGeolocation() {
    this.#removeModal.open();
  }

  #resetGeolocation() {
    this.#resetModal.open();
  }

  #removeConfirmGeolocation() {
    if (this.#savedLocations.length > 0) {
      this.#savedLocations.pop();
      this.#renderHtml('Deleted Last Location');
      GpsMeasure.#setState(this.#savedLocations);
    }
  }

  #resetConfirmGeolocation() {
    if (this.#savedLocations.length > 1) {
      this.#savedLocations.length = 0;
      this.#renderHtml('Deleted All Locations');
      GpsMeasure.#setState(this.#savedLocations);
    }
  }

  #watchPositionSuccess(position) {
    const {
      coords: { accuracy, latitude, longitude, speed },
      timestamp,
    } = position;

    this.#position = {
      accuracy,
      latitude,
      longitude,
      speed,
    };

    this.#renderHtml(`Updated @ ${new Date(timestamp).toLocaleTimeString()}`);
  }

  #watchPositionError(error) {
    const { code, message } = error;

    console.error({ error, message });

    switch (code) {
      case 3: // timeout
        this.#renderHtml(message);
        break;
      case 1: // permissions
      case 2: // unavailable
      default:
        // todo handle all unrecoverable errors
        this.#stopGeolocation(message, false);
    }
  }

  #renderHtml(status = '') {
    let currentCoords = '------ / ------';
    let currentLatitude = 0;
    let currentLongitude = 0;
    let currentSpeed = 0;
    let currentDistance = 0;
    let currentAccuracy = 0;
    let savedDistance = 0;
    let savedAccuracy = 0;
    let totalDistance = 0;
    let totalAccuracy = 0;
    const validationErrors = [];
    const savedCount = this.#savedLocations.length;

    if (this.#position !== null) {
      const { accuracy, latitude, longitude, speed } = this.#position;

      this.#position = null;

      let distance = 0;

      if (savedCount > 0) {
        const { latitude: lat1, longitude: lon1 } = this.#savedLocations[savedCount - 1];

        distance = GpsMeasure.#distanceBetweenCoords(lat1, lon1, latitude, longitude);
      }

      this.#location = { accuracy, latitude, longitude, speed, distance };
    }

    if (this.#location !== null) {
      const { accuracy, latitude, longitude, speed, distance } = this.#location;

      currentLatitude = latitude;
      currentLongitude = longitude;
      currentDistance = distance;
      currentAccuracy = accuracy;

      currentCoords = `${currentLatitude.toFixed(4)} / ${currentLongitude.toFixed(4)}`;

      if (speed !== null) {
        currentSpeed = speed;
      }

      if (accuracy > GpsMeasure.#MAGIC.accuracyMax) {
        validationErrors.push('Poor GPS Signal');
        this.#dom.currentAccuracy.classList.add('invalid');
      } else {
        this.#dom.currentAccuracy.classList.remove('invalid');
      }

      if (speed !== null && speed > GpsMeasure.#MAGIC.speedMax) {
        validationErrors.push('Speed Too Fast');
        this.#dom.currentSpeed.classList.add('invalid');
      } else {
        this.#dom.currentSpeed.classList.remove('invalid');
      }

      if (savedCount > 0 && distance < GpsMeasure.#MAGIC.distanceMin) {
        validationErrors.push('Distance Too Short');
        this.#dom.currentDistance.classList.add('invalid');
      } else {
        this.#dom.currentDistance.classList.remove('invalid');
      }

      this.#dom.add.disabled = validationErrors.length > 0;
    } else {
      this.#dom.add.disabled = true;

      this.#dom.currentAccuracy.classList.remove('invalid');

      this.#dom.currentSpeed.classList.remove('invalid');

      this.#dom.currentDistance.classList.remove('invalid');
    }

    if (savedCount === 0) {
      this.#dom.remove.disabled = true;
      this.#dom.reset.disabled = true;
    } else if (savedCount === 1) {
      this.#dom.remove.disabled = false;
      this.#dom.reset.disabled = true;
    } else if (savedCount > 1) {
      this.#dom.remove.disabled = false;
      this.#dom.reset.disabled = false;
    }

    document.documentElement.style.setProperty(
      '--saved-count',
      `'${savedCount > 0 ? `(${savedCount.toString()})` : ''}'`
    );

    this.#savedLocations.forEach((location) => {
      savedDistance += location.distance;
      savedAccuracy += location.accuracy;
    });

    currentSpeed = (currentSpeed * GpsMeasure.#MAGIC.mpsToMph).toFixed(1);

    currentDistance = ~~(currentDistance * GpsMeasure.#MAGIC.mToFt);

    currentAccuracy = ~~(currentAccuracy * GpsMeasure.#MAGIC.mToFt);

    savedDistance = ~~(savedDistance * GpsMeasure.#MAGIC.mToFt);

    savedAccuracy = ~~(savedAccuracy * GpsMeasure.#MAGIC.mToFt);

    totalDistance = currentDistance + savedDistance;

    totalAccuracy = currentAccuracy + savedAccuracy;

    this.#dom.currentStatus.innerText = status;
    this.#dom.currentSpeed.innerText = currentSpeed;
    this.#dom.currentCoords.innerText = currentCoords;
    this.#dom.currentDistance.innerText = currentDistance;
    this.#dom.currentAccuracy.innerText = currentAccuracy;
    this.#dom.savedDistance.innerText = savedDistance;
    this.#dom.savedAccuracy.innerText = savedAccuracy;
    this.#dom.totalDistance.innerText = totalDistance;
    this.#dom.totalAccuracy.innerText = totalAccuracy;
  }
}

if (!customElements.get('gps-measure')) {
  customElements.define('gps-measure', GpsMeasure);
}
