import template from './template';
// import { alertEmphasis } from '../../util';

export default class VradPort extends HTMLElement {
  static #LOCAL_STORAGE_ITEM = 'com.codedbykevin.telcotools.vrad-port';

  static #magic = {
    bankVal: 200,
    slotVal: [100, 48],
    pairRange: 795,
    invalidPairs: [
      96,
      97,
      98,
      99,
      196,
      197,
      198,
      199,
      296,
      297,
      298,
      299,
      396,
      397,
      398,
      399,
      496,
      497,
      498,
      499,
      596,
      597,
      598,
      599,
      696,
      697,
      698,
      699,
    ],
  };

  #dom = null;

  #initialized = false;

  constructor() {
    super();
  }

  connectedCallback() {
    if (this.#initialized !== true) {
      const state = VradPort.#getState();

      this.innerHTML = template.html(state);

      this.#dom = template.mapDOM(this);

      this.#initialized = true;
    }

    this.#addHandlers();
  }

  disconnectedCallback() {
    this.#removeHandlers();
  }

  #addHandlers() {
    this.#dom.vradPortEquipment.onchange = (e) => this.#onchange(e);

    this.#dom.vradPortAssignment.oninput = (e) => this.#oninput(e);
  }

  #removeHandlers() {
    this.#dom.vradPortEquipment.onchange = null;

    this.#dom.vradPortAssignment.oninput = null;
  }

  #onchange(e) {
    this.#clearErrors();

    this.#calculateFromEquipment();
  }

  #oninput(e) {
    this.#clearErrors();

    this.#calculateFromAssignment();
  }

  #clearErrors() {
    this.#dom.vradPortStartError.innerText = '';

    this.#dom.vradPortStartError.classList.remove('is-visible');

    this.#dom.vradPortPairError.innerText = '';

    this.#dom.vradPortPairError.classList.remove('is-visible');
  }

  #validateStart() {
    const min = parseInt(this.#dom.vradPortStart.min);

    const max = parseInt(this.#dom.vradPortStart.max);

    const value = parseInt(this.#dom.vradPortStart.value);

    if (isNaN(value) || value < min || value > max) {
      return [new Error(`"Start" must range from ${min} to ${max}`), null];
    }

    return [null, { min, max, value }];
  }

  #validatePair() {
    const min = parseInt(this.#dom.vradPortPair.min);

    const max = parseInt(this.#dom.vradPortPair.max);

    const value = parseInt(this.#dom.vradPortPair.value);

    if (isNaN(value) || value < min || value > max) {
      return [new Error(`"Pair" must range from ${min} to ${max}`), null];
    }

    const index = value - min;

    if (VradPort.#magic.invalidPairs.includes(index)) {
      return [new Error(`Invalid pair ${value}`), null];
    }

    return [null, { min, max, value }];
  }

  #calculateFromEquipment() {
    const [err, result] = this.#validateStart();

    if (err) {
      this.#dom.vradPortStartError.innerText = err.message || err.toString();

      this.#dom.vradPortStartError.classList.add('is-visible');

      return;
    }

    const { value: startNum } = result;

    const bankNum = parseInt(this.#dom.vradPortBank.value);

    const slotNum = parseInt(this.#dom.vradPortSlot.value);

    const positionNum = parseInt(this.#dom.vradPortPosition.value);

    let pairNum = startNum;

    pairNum += bankNum;

    pairNum += slotNum;

    pairNum += positionNum;

    this.#dom.vradPortPair.value = pairNum;

    VradPort.#setState({
      startNum,
      pairNum,
      bankNum,
      slotNum,
      positionNum,
    });
  }

  #calculateFromAssignment() {
    let [err, result] = this.#validateStart();

    if (err) {
      this.#dom.vradPortStartError.innerText = err.message || err.toString();

      this.#dom.vradPortStartError.classList.add('is-visible');

      return;
    }

    const { value: startNum } = result;

    this.#dom.vradPortPair.min = startNum;

    this.#dom.vradPortPair.max = startNum + VradPort.#magic.pairRange;

    [err, result] = this.#validatePair();

    if (err) {
      this.#dom.vradPortPairError.innerText = err.message || err.toString();

      this.#dom.vradPortPairError.classList.add('is-visible');

      return;
    }

    const { value: pairNum } = result;

    const index = pairNum - startNum;

    const bankNum = ~~(index / VradPort.#magic.bankVal) * VradPort.#magic.bankVal;

    let slotNum = ~~((index - bankNum) / VradPort.#magic.slotVal[0]) * VradPort.#magic.slotVal[0];

    slotNum += ~~((index - bankNum - slotNum) / VradPort.#magic.slotVal[1]) * VradPort.#magic.slotVal[1];

    const positionNum = index - bankNum - slotNum;

    this.#dom.vradPortBank.value = bankNum;

    this.#dom.vradPortSlot.value = slotNum;

    this.#dom.vradPortPosition.value = positionNum;

    VradPort.#setState({
      startNum,
      pairNum,
      bankNum,
      slotNum,
      positionNum,
    });
  }

  static #setState(state) {
    localStorage.setItem(VradPort.#LOCAL_STORAGE_ITEM, JSON.stringify(state));
  }

  static #getState() {
    const item = localStorage.getItem(VradPort.#LOCAL_STORAGE_ITEM);

    if (item) {
      return JSON.parse(item);
    }

    return {
      startNum: 1,
      pairNum: 1,
      bankNum: 0,
      slotNum: 0,
      positionNum: 0,
    };
  }
}

if (!customElements.get('vrad-port')) {
  customElements.define('vrad-port', VradPort);
}
