import template from './template';
// import { alertEmphasis } from '../../util';

export default class EvenPic extends HTMLElement {
  static #LOCAL_STORAGE_ITEM = 'com.codedbykevin.telcotools.even-pic';

  static #magic = { superVal: 600, binderVal: 25, pairRange: 4199 };

  #dom = null;

  #initialized = false;

  constructor() {
    super();
  }

  connectedCallback() {
    // console.log('connected', this);

    if (this.#initialized !== true) {
      const state = EvenPic.#getState();

      this.innerHTML = template.html(state);

      this.#dom = template.mapDOM(this);

      this.#initialized = true;
    }

    this.#addHandlers();
  }

  disconnectedCallback() {
    // console.log('disconnected', this);

    this.#removeHandlers();
  }

  #addHandlers() {
    this.#dom.evenPicColor.onchange = (e) => this.#onchange(e);
    // todo test oninput vs onchange

    this.#dom.evenPicAssignment.oninput = (e) => this.#oninput(e);
  }

  #removeHandlers() {
    this.#dom.evenPicColor.onchange = null;

    this.#dom.evenPicAssignment.oninput = null;
  }

  #onchange(e) {
    this.#clearErrors();

    this.#calculateFromColors();
  }

  #oninput(e) {
    this.#clearErrors();

    this.#calculateFromAssignment();
  }

  #clearErrors() {
    this.#dom.evenPicStartError.innerText = '';

    this.#dom.evenPicStartError.classList.remove('is-visible');

    this.#dom.evenPicPairError.innerText = '';

    this.#dom.evenPicPairError.classList.remove('is-visible');
  }

  #validateStart() {
    const min = parseInt(this.#dom.evenPicStart.min);

    const max = parseInt(this.#dom.evenPicStart.max);

    const value = parseInt(this.#dom.evenPicStart.value);

    if (isNaN(value) || value < min || value > max) {
      return [new Error(`"Start" must range from ${min} to ${max}`), null];
    }

    return [null, { min, max, value }];
  }

  #validatePair() {
    const min = parseInt(this.#dom.evenPicPair.min);

    const max = parseInt(this.#dom.evenPicPair.max);

    const value = parseInt(this.#dom.evenPicPair.value);

    if (isNaN(value) || value < min || value > max) {
      return [new Error(`"Pair" must range from ${min} to ${max}`), null];
    }

    return [null, { min, max, value }];
  }

  #calculateFromColors() {
    const [err, result] = this.#validateStart();

    if (err) {
      this.#dom.evenPicStartError.innerText = err.message || err.toString();

      this.#dom.evenPicStartError.classList.add('is-visible');

      return;
    }

    const { value: startNum } = result;

    const superNum = parseInt(this.#dom.evenPicSuper.value);

    const binderNum = parseInt(this.#dom.evenPicBinder.value);

    const positionNum = parseInt(this.#dom.evenPicPosition.value);

    let pairNum = startNum;

    pairNum += superNum;

    pairNum += binderNum;

    pairNum += positionNum;

    this.#dom.evenPicPair.value = pairNum;

    EvenPic.#setState({
      startNum,
      pairNum,
      superNum,
      binderNum,
      positionNum,
    });
  }

  #calculateFromAssignment() {
    let [err, result] = this.#validateStart();

    if (err) {
      this.#dom.evenPicStartError.innerText = err.message || err.toString();

      this.#dom.evenPicStartError.classList.add('is-visible');

      return;
    }

    const { value: startNum } = result;

    this.#dom.evenPicPair.min = startNum;

    this.#dom.evenPicPair.max = startNum + EvenPic.#magic.pairRange;

    [err, result] = this.#validatePair();

    if (err) {
      this.#dom.evenPicPairError.innerText = err.message || err.toString();

      this.#dom.evenPicPairError.classList.add('is-visible');

      return;
    }

    const { value: pairNum } = result;

    const index = pairNum - startNum;

    const superNum = ~~(index / EvenPic.#magic.superVal) * EvenPic.#magic.superVal;

    const binderNum = ~~((index - superNum) / EvenPic.#magic.binderVal) * EvenPic.#magic.binderVal;

    const positionNum = index - superNum - binderNum;

    this.#dom.evenPicSuper.value = superNum;

    this.#dom.evenPicBinder.value = binderNum;

    this.#dom.evenPicPosition.value = positionNum;

    EvenPic.#setState({
      startNum,
      pairNum,
      superNum,
      binderNum,
      positionNum,
    });
  }

  static #setState(state) {
    localStorage.setItem(EvenPic.#LOCAL_STORAGE_ITEM, JSON.stringify(state));
  }

  static #getState() {
    const item = localStorage.getItem(EvenPic.#LOCAL_STORAGE_ITEM);

    if (!item) {
      return {
        startNum: 1,
        pairNum: 1,
        superNum: 0,
        binderNum: 0,
        positionNum: 0,
      };
    }

    return JSON.parse(item);
  }
}

if (!customElements.get('even-pic')) {
  customElements.define('even-pic', EvenPic);
}
