const positionOptions = [
  { text: '1 Blue', value: 0 },
  { text: '2 Orange', value: 1 },
  { text: '3 Green', value: 2 },
  { text: '4 Brown', value: 3 },
  { text: '5 Slate', value: 4 },
  { text: '6 White', value: 5 },
  { text: '7 Red', value: 6 },
  { text: '8 Black', value: 7 },
  { text: '9 Yellow', value: 8 },
  { text: '10 Violet', value: 9 },
  { text: '11 Rose', value: 10 },
  { text: '12 Aqua', value: 11 },
];

const optionsFromArray = (arr, val) => {
  let options = '';
  arr.forEach((item) => {
    options += `<option value="${item.value}" ${item.value === val ? 'selected="selected"' : ''}>${item.text}</option>`;
  });
  return options;
};

const strandRange = 431;

export default {
  mapDOM(scope) {
    return {
      fiberCableColor: scope.querySelector('#fiberCableColor'),
      fiberCableBinder: scope.querySelector('#fiberCableBinder'),
      fiberCablePosition: scope.querySelector('#fiberCablePosition'),
      fiberCableAssignment: scope.querySelector('#fiberCableAssignment'),
      fiberCableStart: scope.querySelector('#fiberCableStart'),
      fiberCableStrand: scope.querySelector('#fiberCableStrand'),
      fiberCableStartError: scope.querySelector('#fiberCableStartError'),
      fiberCableStrandError: scope.querySelector('#fiberCableStrandError'),
    };
  },

  html(state) {
    return `<div id="fiberCable" class="app flex-container align-center-middle">
      <div id="fiberCableColor" class="item bg-light-blue radius bordered shadow landscape-equal">
        <div class="input-group">
          <label class="input-group-label" for="fiberCableBinder">Binder:</label>
          <select class="input-group-field" id="fiberCableBinder">
            ${optionsFromArray(binderOptions, state.binderNum)}
          </select>
        </div>
        <div class="input-group">
          <label class="input-group-label" for="fiberCablePosition">Position:</label>
          <select class="input-group-field" id="fiberCablePosition">
            ${optionsFromArray(positionOptions, state.positionNum)}
          </select>
        </div>
      </div>
      <span class="equals"></span>
      <div id="fiberCableAssignment" class="item bg-light-blue radius bordered shadow landscape-equal">
        <div class="input-group">
          <label class="input-group-label" for="fiberCableStart">Start:</label>
          <input class="input-group-field" id="fiberCableStart" type="number" inputmode="numeric" placeholder="1234" min="1" max="9999" maxlength="4" pattern="number" value="${
            state.startNum
          }"/>
        </div>
        <span id="fiberCableStartError" class="form-error"></span>
        <div class="input-group">
          <label class="input-group-label" for="fiberCableStrand">Strand:</label>
          <input class="input-group-field" id="fiberCableStrand" type="number" inputmode="numeric" placeholder="1234" min="${
            state.startNum
          }" max="${state.startNum + strandRange}" maxlength="5" pattern="number" value="${state.strandNum}"/>
        </div>
        <span id="fiberCableStrandError" class="form-error"></span>
      </div>
    </div>`;
  },
};
