'use strict';

let iosCanInstall = 'standalone' in window.navigator;
let iosInstalled = false;
let androidInstalled = false;
let chromeInstalled = false;

if (iosCanInstall === true && window.navigator.standalone === true) {
  iosInstalled = true;
} else if (window.document.referrer.includes('android-app://') === true) {
  androidInstalled = true;
} else if (window.matchMedia('(display-mode: standalone)').matches === true) {
  chromeInstalled = true;
}

const installed = iosInstalled || androidInstalled || chromeInstalled;

export { iosCanInstall, iosInstalled, androidInstalled, chromeInstalled, installed };
