import template from './template';

export default class CableComp extends HTMLElement {
  static #LOCAL_STORAGE_ITEM = 'com.codedbykevin.telcotools.cable-comp';

  #dom = null;

  #initialized = false;

  constructor() {
    super();
  }

  connectedCallback() {
    if (this.#initialized !== true) {
      const state = CableComp.#getState();

      this.innerHTML = template.html(state);

      this.#dom = template.mapDOM(this);

      this.#initialized = true;
    }

    this.#addHandlers();
  }

  disconnectedCallback() {
    this.#removeHandlers();
  }

  #addHandlers() {
    this.#dom.selection.onchange = (e) => this.#onchange(e);
  }

  #removeHandlers() {
    this.#dom.selection.onchange = null;
  }

  #onchange(e) {
    const {
      target: {
        options,
        selectedIndex,
        dataset: { type },
      },
    } = e;

    const {
      dataset: { details },
    } = options[selectedIndex];

    switch (type) {
      case 'design':
        this.#dom.designDetails.value = details;
        break;
      case 'insulation':
        this.#dom.insulationDetails.value = details;
        break;
      case 'gauge':
        this.#dom.gaugeDetails.value = details;
        break;
      case 'sheath':
        this.#dom.sheathDetails.value = details;
        break;
    }

    CableComp.#setState({
      designSelection: this.#dom.designSelection.value,
      designDetails: this.#dom.designDetails.value,
      insulationSelection: this.#dom.insulationSelection.value,
      insulationDetails: this.#dom.insulationDetails.value,
      gaugeSelection: this.#dom.gaugeSelection.value,
      gaugeDetails: this.#dom.gaugeDetails.value,
      sheathSelection: this.#dom.sheathSelection.value,
      sheathDetails: this.#dom.sheathDetails.value,
    });
  }

  static #setState(state) {
    localStorage.setItem(CableComp.#LOCAL_STORAGE_ITEM, JSON.stringify(state));
  }

  static #getState() {
    const item = localStorage.getItem(CableComp.#LOCAL_STORAGE_ITEM);

    if (!item) {
      return {
        designSelection: 'B',
        designDetails: 'PIC Aircore',
        insulationSelection: 'K',
        insulationDetails: 'Solid Polyolefin Aircore(24 - 28 AWG)',
        gaugeSelection: 'M',
        gaugeDetails: '24 AWG Copper',
        sheathSelection: 'P',
        sheathDetails: 'Reinforced Self Support',
      };
    }

    return JSON.parse(item);
  }
}

if (!customElements.get('cable-comp')) {
  customElements.define('cable-comp', CableComp);
}
