import template from './template';
// import { alertEmphasis } from '../../util';

export default class LightspanPort extends HTMLElement {
  static #LOCAL_STORAGE_ITEM = 'com.codedbykevin.telcotools.lightspan-port';

  static #magic = {
    bankVal: [450, 226],
    slotVal: 4,
    pairRange: 2023,
    invalidPairs: [224, 225, 674, 675, 1124, 1125, 1574, 1575],
  };

  #dom = null;

  #initialized = false;

  constructor() {
    super();
  }

  connectedCallback() {
    if (this.#initialized !== true) {
      const state = LightspanPort.#getState();

      this.innerHTML = template.html(state);

      this.#dom = template.mapDOM(this);

      this.#initialized = true;
    }

    this.#addHandlers();
  }

  disconnectedCallback() {
    this.#removeHandlers();
  }

  #addHandlers() {
    this.#dom.lightspanPortEquipment.onchange = (e) => this.#onchange(e);

    this.#dom.lightspanPortAssignment.oninput = (e) => this.#oninput(e);
  }

  #removeHandlers() {
    this.#dom.lightspanPortEquipment.onchange = null;

    this.#dom.lightspanPortAssignment.oninput = null;
  }

  #onchange(e) {
    this.#clearErrors();

    this.#calculateFromEquipment();
  }

  #oninput(e) {
    this.#clearErrors();

    this.#calculateFromAssignment();
  }

  #clearErrors() {
    this.#dom.lightspanPortStartError.innerText = '';

    this.#dom.lightspanPortStartError.classList.remove('is-visible');

    this.#dom.lightspanPortPairError.innerText = '';

    this.#dom.lightspanPortPairError.classList.remove('is-visible');
  }

  #validateStart() {
    const min = parseInt(this.#dom.lightspanPortStart.min);

    const max = parseInt(this.#dom.lightspanPortStart.max);

    const value = parseInt(this.#dom.lightspanPortStart.value);

    if (isNaN(value) || value < min || value > max) {
      return [new Error(`"Start" must range from ${min} to ${max}`), null];
    }

    return [null, { min, max, value }];
  }

  #validatePair() {
    const min = parseInt(this.#dom.lightspanPortPair.min);

    const max = parseInt(this.#dom.lightspanPortPair.max);

    const value = parseInt(this.#dom.lightspanPortPair.value);

    if (isNaN(value) || value < min || value > max) {
      return [new Error(`"Pair" must range from ${min} to ${max}`), null];
    }

    const index = value - min;

    if (LightspanPort.#magic.invalidPairs.includes(index)) {
      return [new Error(`Invalid pair ${value}`), null];
    }

    return [null, { min, max, value }];
  }

  #calculateFromEquipment() {
    const [err, result] = this.#validateStart();

    if (err) {
      this.#dom.lightspanPortStartError.innerText = err.message || err.toString();

      this.#dom.lightspanPortStartError.classList.add('is-visible');

      return;
    }

    const { value: startNum } = result;

    const bankNum = parseInt(this.#dom.lightspanPortBank.value);

    const slotNum = parseInt(this.#dom.lightspanPortSlot.value);

    const positionNum = parseInt(this.#dom.lightspanPortPosition.value);

    let pairNum = startNum;

    pairNum += bankNum;

    pairNum += slotNum;

    pairNum += positionNum;

    this.#dom.lightspanPortPair.value = pairNum;

    LightspanPort.#setState({
      startNum,
      pairNum,
      bankNum,
      slotNum,
      positionNum,
    });
  }

  #calculateFromAssignment() {
    let [err, result] = this.#validateStart();

    if (err) {
      this.#dom.lightspanPortStartError.innerText = err.message || err.toString();

      this.#dom.lightspanPortStartError.classList.add('is-visible');

      return;
    }

    const { value: startNum } = result;

    this.#dom.lightspanPortPair.min = startNum;

    this.#dom.lightspanPortPair.max = startNum + LightspanPort.#magic.pairRange;

    [err, result] = this.#validatePair();

    if (err) {
      this.#dom.lightspanPortPairError.innerText = err.message || err.toString();

      this.#dom.lightspanPortPairError.classList.add('is-visible');

      return;
    }

    const { value: pairNum } = result;

    const index = pairNum - startNum;

    let bankNum = ~~(index / LightspanPort.#magic.bankVal[0]) * LightspanPort.#magic.bankVal[0];

    bankNum += ~~((index - bankNum) / LightspanPort.#magic.bankVal[1]) * LightspanPort.#magic.bankVal[1];

    const slotNum = ~~((index - bankNum) / LightspanPort.#magic.slotVal) * LightspanPort.#magic.slotVal;

    const positionNum = index - bankNum - slotNum;

    this.#dom.lightspanPortBank.value = bankNum;

    this.#dom.lightspanPortSlot.value = slotNum;

    this.#dom.lightspanPortPosition.value = positionNum;

    LightspanPort.#setState({
      startNum,
      pairNum,
      bankNum,
      slotNum,
      positionNum,
    });
  }

  static #setState(state) {
    localStorage.setItem(LightspanPort.#LOCAL_STORAGE_ITEM, JSON.stringify(state));
  }

  static #getState() {
    const item = localStorage.getItem(LightspanPort.#LOCAL_STORAGE_ITEM);

    if (!item) {
      return {
        startNum: 1,
        pairNum: 1,
        bankNum: 0,
        slotNum: 0,
        positionNum: 0,
      };
    }

    return JSON.parse(item);
  }
}

if (!customElements.get('lightspan-port')) {
  customElements.define('lightspan-port', LightspanPort);
}
