import template from './template';
import { CenteredReveal } from '../../lib/CenteredReveal';
import { $ } from '../../vendor/jquery';
import { globalEventMap } from '../../lib/globalEventMap';

export default class IosPwaInstall extends HTMLElement {
  static #LOCAL_STORAGE_ITEM = 'com.codedbykevin.telcotools.ios-pwa-install';

  static #MAGIC = {
    minTimeout: 60 * 1000, // = 1 minute
    nextTime: 24 * 60 * 60 * 1000, // = 1 day
  };

  #dom = null;

  #initialized = false;

  #modal = null;

  #resizeSymbol = null;

  #setTimeout = null;

  #timeout = null;

  constructor() {
    super();
  }

  connectedCallback() {
    if (this.#initialized !== true) {
      const { whenToNudge } = IosPwaInstall.#getState();

      const now = Date.now();

      if (Number.isNaN(whenToNudge) === true || whenToNudge < now + IosPwaInstall.#MAGIC.minTimeout) {
        this.#timeout = IosPwaInstall.#MAGIC.minTimeout;
      } else {
        this.#timeout = whenToNudge - now;
      }

      this.innerHTML = template.html();

      this.#dom = template.mapDOM(this);

      this.#modal = new CenteredReveal($(this.#dom.iosPwaInstallModal), {
        appendTo: this,
        closeOnClick: false,
        additionalOverlayClasses: '',
      });

      this.#resizeSymbol = Symbol('resize');

      this.#initialized = true;
    }

    this.#addHandlers();
    this.#startTimer();
  }

  disconnectedCallback() {
    this.#removeHandlers();
    this.#stopTimer();
  }

  #startTimer() {
    this.#setTimeout = setTimeout(() => {
      this.#modal.open();

      IosPwaInstall.#setState(Date.now() + IosPwaInstall.#MAGIC.nextTime);
      this.#setTimeout = null;
    }, this.#timeout);
  }

  #stopTimer() {
    clearTimeout(this.#setTimeout);
    this.#setTimeout = null;
  }

  #addHandlers() {
    const onResizeBound = () => this.#onResize();
    globalEventMap.set(this.#resizeSymbol, onResizeBound);
    window.addEventListener('resize', onResizeBound);

    $(this).on('closed.zf.reveal', this.#onClosed);
  }

  #removeHandlers() {
    window.removeEventListener('resize', globalEventMap.get(this.#resizeSymbol));
    globalEventMap.delete(this.#resizeSymbol);

    $(this).off('closed.zf.reveal', this.#onClosed);
  }

  #onResize() {
    this.#modal.updatePosition();
  }

  #onClosed() {
    this.remove();
  }

  static #setState(state) {
    console.log('set state', state);

    localStorage.setItem(IosPwaInstall.#LOCAL_STORAGE_ITEM, JSON.stringify(state));
  }

  static #getState() {
    const item = localStorage.getItem(IosPwaInstall.#LOCAL_STORAGE_ITEM);

    const whenToNudge = parseInt(item);

    return {
      whenToNudge,
    };
  }
}

if (!customElements.get('ios-pwa-install')) {
  customElements.define('ios-pwa-install', IosPwaInstall);
}
