export default {
  mapDOM(scope) {
    return {
      info: scope.querySelector('#gpsMeasureInfo'),
      start: scope.querySelector('#gpsMeasureStart'),
      stop: scope.querySelector('#gpsMeasureStop'),
      add: scope.querySelector('#gpsMeasureAdd'),
      remove: scope.querySelector('#gpsMeasureRemove'),
      reset: scope.querySelector('#gpsMeasureReset'),
      removeModal: scope.querySelector('#gpsMeasureRemoveModal'),
      resetModal: scope.querySelector('#gpsMeasureResetModal'),
      resumeModal: scope.querySelector('#gpsMeasureResumeModal'),
      removeConfirm: scope.querySelector('#gpsMeasureRemoveModal button.button.alert'),
      resetConfirm: scope.querySelector('#gpsMeasureResetModal button.button.alert'),
      resumeConfirm: scope.querySelector('#gpsMeasureResumeModal button.button.success'),
      currentStatus: scope.querySelector('#gpsCurrentStatus'),
      currentCoords: scope.querySelector('#gpsCurrentCoords'),
      currentSpeed: scope.querySelector('#gpsCurrentSpeed'),
      currentDistance: scope.querySelector('#gpsCurrentDistance'),
      currentAccuracy: scope.querySelector('#gpsCurrentAccuracy'),
      savedDistance: scope.querySelector('#gpsSavedDistance'),
      savedAccuracy: scope.querySelector('#gpsSavedAccuracy'),
      totalDistance: scope.querySelector('#gpsTotalDistance'),
      totalAccuracy: scope.querySelector('#gpsTotalAccuracy'),
    };
  },

  html() {
    return `<div id="gpsMeasure" class="app flex-container align-center-middle">
      <div id="gpsMeasureInfo" class="item bg-light-blue radius bordered shadow landscape-shrink-35">
        <span id="gpsCurrentStatus" class="status bg-light-grey bordered display-block">off</span>
        <span id="gpsCurrentCoords" class="coords bg-light-grey bordered display-block">0/0</span>
        <span id="gpsCurrentSpeed" class="speed mph bg-light-grey bordered display-block">0</span>
        <table id="gpsResults" class="unstriped">
          <thead>
            <tr>
              <th></th>
              <th class="text-center">Distance</th>
              <th class="text-center">Accuracy</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <th class="text-left">Current</th>
              <td class="feet text-center" id="gpsCurrentDistance">0</td>
              <td class="plus-minus feet text-center" id="gpsCurrentAccuracy">0</td>
            </tr>
            <tr>
              <th class="saved-count text-left">Saved</th>
              <td class="feet text-center" id="gpsSavedDistance">0</td>
              <td class="plus-minus feet text-center" id="gpsSavedAccuracy">0</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <th class="text-left">Total</th>
              <td class="feet text-center" id="gpsTotalDistance">0</td>
              <td class="plus-minus feet text-center" id="gpsTotalAccuracy">0</td>
            </tr>
          </tfoot>
        </table>
      </div>
        <div id="gpsMeasureControls" class="item landscape-shrink-auto">
          <div class="controls">
            <button id="gpsMeasureStart" class="control play" aria-label="Start" data-control="start"></button>
            <button id="gpsMeasureStop" class="control stop is-hidden" aria-label="Stop" data-control="stop"></button>
            <button id="gpsMeasureAdd" class="control plus" disabled aria-label="Add" data-control="add"></button>
            <button id="gpsMeasureRemove" class="control minus" disabled aria-label="Remove" data-control="remove"></button>
            <button id="gpsMeasureReset" class="control undo" disabled aria-label="Reset" data-control="reset"></button>
          </div>
        </div>
      </div>

      <div class="reveal" id="gpsMeasureRemoveModal">
        <h4>Delete LAST saved location?</h4>
        <p>This action cannot be undone.</p>
        <div class="button-group expanded" data-close>
          <button class="button success" aria-label="Cancel" type="button">Cancel</button>
          <button class="button alert" aria-label="Delete" type="button">Delete</button>
        </div>
      </div>

      <div class="reveal" id="gpsMeasureResetModal">
        <h4>Delete ALL saved locations?</h4>
        <p>This action cannot be undone.</p>
        <div class="button-group expanded" data-close>
          <button class="button success" aria-label="Cancel" type="button">Cancel</button>
          <button class="button alert" aria-label="Delete" type="button">Delete</button>
        </div>
      </div>

      <div class="reveal" id="gpsMeasureResumeModal">
        <h4>Resume geolocation service?</h4>
        <div class="button-group expanded" data-close>
          <button class="button alert" aria-label="Cancel" type="button">Cancel</button>
          <button class="button success" aria-label="Resume" type="button">Resume</button>
        </div>
      </div>`;
  },
};
